  public static padding(num: any, length: number, option?: any) {
    //æå®ããªããã°0
    option = option || "0";
    //æ°å¤åã«å¤æ
    num = Number(num);
    //æ°å¤ã§ãªããã°nullãè¿ã
    if (isNaN(num)) return null;

    return (Array(length).join(option) + num).slice(-length);

  }

