    'ï½³ï¾': 'ã´', 'ï¾ï¾': 'ã·', 'ï½¦ï¾': 'ãº',
    'ï½±': 'ã¢', 'ï½²': 'ã¤', 'ï½³': 'ã¦', 'ï½´': 'ã¨', 'ï½µ': 'ãª',
    'ï½¶': 'ã«', 'ï½·': 'ã­', 'ï½¸': 'ã¯', 'ï½¹': 'ã±', 'ï½º': 'ã³',
    'ï½»': 'ãµ', 'ï½¼': 'ã·', 'ï½½': 'ã¹', 'ï½¾': 'ã»', 'ï½¿': 'ã½',
    'ï¾': 'ã¿', 'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã',
    'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã',
    'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã', 'ï¾': 'ã',
