  /**
  * å¾æ¥­å¡ã³ã¼ãã®å¤æ(6æ¡â8æ¡)
  * @param s ã³ã¼ãæå­å
  * @return å¤æå¾ã®ã³ã¼ãæå­å
  */
  public static convJyuCode6To8(s: String): String {
    // ãã©ã³ã¯ãªã
    if (this.isBlank(s)) return '';
    // 6æ¡ä»¥å¤å¦çããªã
    if (s.trim().length != 6 && !isNaN(parseInt(s.toString()))) return s;

    let sJyuCode = s;
    let result = '00';

    if (((sJyuCode == '922222') || (sJyuCode == '933333') ||
      (sJyuCode == '944444') || (sJyuCode == '955555') ||
      (sJyuCode == '966666') || (sJyuCode == '977777') ||
      (sJyuCode == '988888')) || ((sJyuCode >= '999000') && (sJyuCode <= '999999'))) {

      result = '99' + sJyuCode;
    } else {
      let strCd = sJyuCode.substr(0, 1).toUpperCase();
      sJyuCode = sJyuCode.replace(/[c|k|s]/g, '');
      if (strCd == 'C' || strCd == 'S') {
        //å·¦1æå­ã[C]ã[S]ã®ã¨ã
        let subNumber = sJyuCode.substr(2, 2);
        //å·¦ã«æå­ç®ããèªåå«ãï¼æå­ç®ãã
        if (Number(subNumber) >= 0 && Number(subNumber) <= 49) {
          //>= 0 ããã³ã <=49 ã®å ´å
          result = '20' + sJyuCode;
        } else {
          //>= 0 ããã³ã <=49 ã§ãªãå ´å
          result = '19' + sJyuCode;
        }
      } else if (strCd == 'K') {
        //å·¦1æå­ã[C]ã[S]ã®ã¨ãã§ãªã[K]ã®å ´å
        result = '00' + sJyuCode;
      } else {
        //å·¦1æå­ãä¸è¨ä»¥å¤
        let rStr = sJyuCode.substr(-2, 2);
        //å·¦ï¼æå­ã
        if (Number(rStr) >= 0 && Number(rStr) <= 43) {
          //     >= 0 åã³ã<= 43 ã®å ´å
          result = '20' + sJyuCode;
        } else {
          //>= 0 åã³ã<= 43 ã§ãªãå ´å
          result = '19' + sJyuCode;
        }
      }
    }
    // æ´å½¢æå­è¿å´
    return result;

  }

