import { Component, OnChanges, SimpleChanges, OnInit } from "@angular/core";
import { DateAdapter, NativeDateAdapter } from "@angular/material";
import { JpDateAdapter } from "./adapter/jpDateAdapter";
import { SHFormControl } from "../../utilities/ShFormControl";

@Component({
  selector: "sh-ctl-date",
  templateUrl: "./sh-ctl-date.component.html",
  styleUrls: ["./sh-ctl-date.component.css", "../../../style.common.css"],
  providers: [{ provide: DateAdapter, useClass: JpDateAdapter }]
})
export class SHCtlDateComponent extends SHFormControl<Date> implements OnInit, OnChanges {
  public stringValue: any;

  constructor(dataAdapter: DateAdapter<NativeDateAdapter>) {
    super();
    this.ctlName = "SHCtlDate";
    dataAdapter.setLocale("ja");
  }

  public ngOnInit() {
    this.onInit();
  }
  
  public ngOnChanges(changes: SimpleChanges) {
    if (changes.value) {
      if (changes.value.currentValue) {
        const result = this.getfomatedDate(changes.value.currentValue.replace(/\-/g, '/'));
        this.value = new Date(result);
      }
    }
  }

  public ngAfterContentChecked() {
    if (this.value != null && this.isDisabled) {
      this.stringValue = this.changeFormat(this.value);
    }
  }

  /**
   * onOpened
   */
  public onOpened() {
    let el = document.querySelector("mat-datepicker-content");
    el.addEventListener("focusin", () => {
      this.isfocused = true;
    });
  }

  /**
   * onClosed
   */
  public onClosed() {
    let inputValue = <HTMLInputElement>document.querySelector(`textarea[name=` + this.ctlName + `], input[name=` + this.ctlName + `]`);
    setTimeout(function(){inputValue.dispatchEvent(new Event('focus'));},0);  
  }

  /**
   * onKeypress
   * @param event keyPress
   */
  public onKeypress(event) {
    this.setCurrentDate(event);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);

    this.valueChange.emit(this.getfomatedDate(event.target.value));

    setTimeout(() => {
      this.onAfterChange(event);
    }, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  /**
   * æ¬æ¥æ¥ä»ã®è¨­å®
   */
  public setCurrentDate(event) {
    if (event.key === "/") {
      this.value = new Date();
      event.target.value = this.getfomatedDate(this.value).split('/').join('-');
      this.onChange(event);
    }
  }

  /**
   * æ¥ä»ãã¼ã¿ã®ãã©ã¼ãããå¤æ´ãè¡ã
   * @param date æ¥ä»ãã¼ã¿
   * @return æ¥ä»æå­å
   */
  private getfomatedDate(date) {
    if (!date) return "";
    const toString = Object.prototype.toString;
    if (toString.call(date) == "[object Date]") {
      const { year, month, day } = this.paddingDate(date.toLocaleDateString());
      return `${year}/${month}/${day}`;
    } 
    else {
      const { year, month, day } = this.paddingDate(date);
      if (date.match(/-/))
        return `${year}/${month}/${day}`;
      else
        return `${year}-${month}-${day}`;
    }
  }

  /**
   * æ¥ä»æå­åã®å¹´ææ¥åå²ãªãã¸ã§ã¯ããè¿å´ãã
   * @param dateStr æ¥ä»æå­å
   * @return åå²ãªãã¸ã§ã¯ã(year, month, day)
   */
  public paddingDate(dateStr: string) {
    if (!dateStr) return;
    const dateArr = dateStr.split(/[-/]/)
    let year = (Array(4).join("0") + dateArr[0]).slice(-4);
    const month = (Array(2).join('0') + dateArr[1]).slice(-2);
    const day = (Array(2).join('0') + dateArr[2]).slice(-2);
    return { year, month, day }
  }

  /**
   * ãã©ã¼ãããç·¨é
   * @param value 
   */
  public changeFormat(value: Date) {
    if (!value) {
      this.stringValue = "";
      return;
    }
    const dataStr = value.toLocaleDateString();
    const result = dataStr.replace('/', "å¹´").replace('/', 'æ') + 'æ¥';
    return result;
  }
}
